import React, { memo, useEffect, useState } from 'react'
import { AspectRatio, Flex, Stack } from '@chakra-ui/layout'
import { csv } from 'd3-fetch'

import chartData from '../../data'
import Text from '../../components/Text'
import Box from '../../components/Box'
import Carousel from '../../components/Carousel'
import { responsive } from '../../components/ThemeProvider/theme'

const useCsv = path => {
  const [data, setData] = useState(null)
  useEffect(() => csv(path).then(setData), [])
  return data
}

const Chart = memo(({ togglable, type, file, title, notes }) => {
  const data = useCsv(file)
  console.log(data)
  return data && (
    <Stack spacing="1em">
      <Text.H3 textAlign="center">{title}</Text.H3>
      <AspectRatio ratio={16 / 9}>
        <Box border="1px solid white" />
      </AspectRatio>
      {togglable && (
        <Flex>
          <Text display={responsive('none', 'inline')}>請依類別篩選</Text>
        </Flex>
      )}
      {notes && (
        <Box>
          {notes.map((n, i) => <Text.Note key={i}>註{i + 1}：{n}</Text.Note>)}
        </Box>
      )}
    </Stack>
  )
})

const Charts = ({ data, ...props }) => {
  const charts = data.map(k => <Chart key={k} {...chartData[k]} />)
  if (charts.length === 1) return <Box {...props}>{charts}</Box>
  return (
    <Box {...props}>
      <Carousel
        width="100%"
        silderProps={{
          infinite: false,
        }}
        charts
      >{charts}</Carousel>
    </Box>
  )
}

export default Charts
